import React from 'react';


function InfoClima (props){

    return(
        <div className="contenedor-info">

            <div className="info">
                {
                    props.temperatura &&
                        <div>
                            <h3>Datos</h3>
                            <p><span className="span">Localización:         </span> {props.ciudad}, {props.pais}</p>
                            <p><span className="span">Tiempo:               </span> {props.descripcion}</p>
                            <p><span className="span">Temperatura:          </span>  {props.temperatura} °C</p>
                            <p><span className="span">Humedad:              </span>{props.humedad}</p>
                            <p><span className="span">Velocidad del Viento: </span>{props.velocidadViento}</p>
                        </div>
            
                   
                    
                }
            </div>
        </div>
    )
}

export default InfoClima;